import { useState } from "react";
import { TextField, Grid, Paper, Button } from "@mui/material";
import { useNavigate } from "react-router-dom";

function DeleteData() {
  const [hospitalCode, setHospitalCode] = useState("");
  const navigate = useNavigate();
  const handleSearch = () => {
    if (!hospitalCode) {
      return;
    }
    navigate(`/delete-table/${hospitalCode}`);
  };
  return (
    <Grid
      container
      justifyContent="center"
      alignItems="center"
      style={{ minHeight: "100vh" }}
    >
      <Paper
        elevation={10}
        sx={{
          padding: 10,
          height: 300,
          width: 370,
        }}
      >
        <Grid container direction="column" alignItems="center">
          <h1>Insert Hospital Code:</h1>
          <TextField
            id="outlined-basic"
            label="Delete Data"
            placeholder="Hospital Code"
            variant="outlined"
            onChange={(e) => setHospitalCode(e.target.value)}
            required
            sx={{
              margin: "50px auto",
              borderRadius: "10%",
              width: "300px",
            }}
          />
          <Button
            type="submit"
            color="primary"
            variant="contained"
            onClick={handleSearch} // Call the handleSearch function on button click
            sx={{ margin: "10px auto" }}
          >
            Search
          </Button>
        </Grid>
      </Paper>
    </Grid>
  );
}
export default DeleteData;
