import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import BASE_URL from "../../helpers/baseURL";
import formatDate from "../../helpers/formatDate";
import { Button } from "@mui/material";
import TablePagination from "@mui/material/TablePagination";

//call api to get the options of buisness partner

const UpdateTable = () => {
  const { hospitalCode } = useParams();
  const hospitalCodeInteger = parseInt(hospitalCode, 10);

  const [rowsPerPage, setRowsPerPage] = useState(8);
  const [page, setPage] = useState(0);

  const [data, setData] = useState([]);
  const [changes, setChanges] = useState({});
  const [oldSerialNumbers, setOldSerialNumbers] = useState([]);
  const [oldBusinessPartners, setOldBusinessPartner] = useState([]);
  const [oldCities, setOldCity] = useState([]);
  const [oldHospitalNames, setOldHospitalName] = useState([]);
  const [selectedRowIndex, setSelectedRowIndex] = useState(-1);

  const startIndex = page * rowsPerPage;
  const endIndex = startIndex + rowsPerPage;

  // Generic function to handle changes in table cells
  const handleCellChange = (index, columnName, event) => {
    const newData = [...data];
    newData[index][columnName] = event.target.value;
    setData(newData);

    // Update the changes state to track the modified columns
    setChanges((prevChanges) => ({
      ...prevChanges,
      [index]: {
        ...prevChanges[index],
        [columnName]: event.target.value,
      },
    }));
  };

  // Function to handle the update button click
  const handleUpdateClick = async () => {
    try {
      if (selectedRowIndex !== -1) {
        const index = selectedRowIndex;
        const modifiedColumns = changes[index];

        if (modifiedColumns) {
          const requestData = {
            ...data[index],
            ...modifiedColumns,
            install_day: formatDate(data[index].install_day),
          };

          const oldSerialNumber = encodeURIComponent(oldSerialNumbers[index]);
          const oldBusinessPartner = encodeURIComponent(
            oldBusinessPartners[index]
          );
          const oldCity = encodeURIComponent(oldCities[index]);
          const oldHospitalName = encodeURIComponent(oldHospitalNames[index]);

          console.log("requestData", requestData);

          const response = await fetch(
            `${BASE_URL}/update/${oldSerialNumber}/${hospitalCode}/${oldBusinessPartner}/${oldCity}/${oldHospitalName}`,
            {
              method: "PUT",
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(requestData),
            }
          );

          const receivedMessage = await response.json();
          setSelectedRowIndex(-1); // Deselect the row
          alert(receivedMessage.message);
          setChanges((prevChanges) => ({
            ...prevChanges,
            [index]: {},
          }));
          fetchData();
        } else {
          alert("No changes to update for the selected row.");
        }
      }
    } catch (error) {
      console.error("Error updating data:", error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const response = await fetch(`${BASE_URL}/hospitalCode/${hospitalCode}`);
      const data = await response.json();
      setData(data);
      setOldSerialNumbers(data.map((item) => item.serial_number));
      setOldBusinessPartner(data.map((item) => item.business_partner));
      setOldCity(data.map((item) => item.city));
      setOldHospitalName(data.map((item) => item.hospital_name));
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  return (
    <Grid
      container
      justifyContent="center"
      alignItems="center"
      style={{ minHeight: "100vh" }}
    >
      <Paper
        elevation={10}
        sx={{
          padding: 3,
          height: 500,
          width: "97%",
          margin: "20px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {data.length === 0 ? (
          <p>No data available for showing.</p>
        ) : (
          <React.Fragment>
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Customer Code
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Business Partner
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Hospital Name
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Customer Type
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>Province</TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>City</TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Equipment Type
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Serial Number
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Install Date
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Warranty Years
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Machine Status
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Contract Type
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>Owner</TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Engineer Name
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {data.slice(startIndex, endIndex).map((item, index) => (
                    <TableRow
                      key={item.serial_number}
                      onClick={() => setSelectedRowIndex(index)}
                      sx={{
                        backgroundColor:
                          selectedRowIndex === index
                            ? "#f0f0f0"
                            : "transparent",
                        cursor: "pointer",
                      }}
                    >
                      <TableCell>
                        <textarea
                          type="number"
                          value={item.customer_code}
                          onChange={(event) =>
                            handleCellChange(index, "customer_code", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.business_partner}
                          onChange={(event) =>
                            handleCellChange(index, "business_partner", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        {" "}
                        <textarea
                          type="text"
                          value={item.hospital_name}
                          onChange={(event) =>
                            handleCellChange(index, "hospital_name", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        {" "}
                        <textarea
                          type="text"
                          value={item.customer_type}
                          onChange={(event) =>
                            handleCellChange(index, "customer_type", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.province}
                          onChange={(event) =>
                            handleCellChange(index, "province", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.city}
                          onChange={(event) =>
                            handleCellChange(index, "city", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.equipment_type}
                          onChange={(event) =>
                            handleCellChange(index, "equipment_type", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.serial_number}
                          onChange={(event) =>
                            handleCellChange(index, "serial_number", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        {" "}
                        <input
                          type="date"
                          value={formatDate(item.install_day)}
                          onChange={(event) =>
                            handleCellChange(index, "install_day", event)
                          }
                          style={{ width: "110px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="number"
                          value={item.number_of_warranty_years}
                          onChange={(event) =>
                            handleCellChange(
                              index,
                              "number_of_warranty_years",
                              event
                            )
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.machine_status}
                          onChange={(event) =>
                            handleCellChange(index, "machine_status", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.contract_type}
                          onChange={(event) =>
                            handleCellChange(index, "contract_type", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>
                        <textarea
                          type="text"
                          value={item.owner}
                          onChange={(event) =>
                            handleCellChange(index, "owner", event)
                          }
                          style={{ width: "80px", height: "50px" }}
                        />
                      </TableCell>
                      <TableCell>{item.employee_name}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
              <TablePagination
                rowsPerPageOptions={[8, 16, 24]}
                component="div"
                count={data.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={(event, newPage) => setPage(newPage)}
                onRowsPerPageChange={(event) => {
                  setRowsPerPage(parseInt(event.target.value, 10));
                  setPage(0);
                }}
              />
            </TableContainer>
            <Button
              type="submit"
              color="primary"
              variant="contained"
              onClick={handleUpdateClick}
              sx={{ margin: "10px auto" }}
            >
              Update Data
            </Button>
          </React.Fragment>
        )}
      </Paper>
    </Grid>
  );
};

export default UpdateTable;
