import React, { useState } from "react";
import { Grid, Paper, Button } from "@mui/material";
import { useEffect } from "react";
import BASE_URL from "../../helpers/baseURL";
import TablePagination from "@mui/material/TablePagination";
import formatDate from "../../helpers/formatDate";

import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";

function AddDataPart1() {
  const [data, setData] = useState([]);
  const [fetchedData, setFetchedData] = useState([]);
  const [changes, setChanges] = useState({});
  const [selectedRowIndex, setSelectedRowIndex] = useState(-1);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [selectedEngineerNumber, setSelectedEngineerNumber] = useState("");
  const [selectedEngineerName, setSelectedEngineerName] = useState("");

  const startIndex = page * rowsPerPage;
  const endIndex = startIndex + rowsPerPage;

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      //fetch data from requests table
      const response = await fetch(`${BASE_URL}/requests`);
      const fetchedData = await response.json();
      setFetchedData(fetchedData);
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  // Generic function to handle changes in table cells
  const handleCellChange = async (columnName, event) => {
    const newData = [...data];
    newData[columnName] = event.target.value;
    setData(newData);

    // Update the changes state to track the modified columns
    setChanges((prevChanges) => ({
      ...prevChanges,
      [columnName]: event.target.value,
    }));
    if (columnName === "employee_number") {
      const selectedNumber = event.target.value;
      setSelectedEngineerNumber(selectedNumber);

      try {
        const response = await fetch(`${BASE_URL}/engineer/${selectedNumber}`);
        const data = await response.json();
        console.log("data", data[0].employee_name);
        setSelectedEngineerName(data[0].employee_name);
        console.log("selectedEngineerName", selectedEngineerName);
      } catch (error) {
        console.error("Error fetching engineer name:", error);
      }
    }
  };

  // Function to handle the update button click
  const handleAddClick = async () => {
    console.log("here");
    // if (selectedRowIndex >= 0 && selectedRowIndex < data.length) {
    try {
      const selectedRow = data[selectedRowIndex];
      console.log("selectedRow", selectedRow);
      const modifiedData = {
        ...data,
        ...fetchedData[selectedRowIndex],
      };

      for (const columnName in changes) {
        modifiedData[columnName] = changes[columnName];
      }
      console.log("change", changes);
      console.log("modifiedData", modifiedData);

      // Make the API request to add to hospital and equipment table and delete from requests table
      const response = await fetch(`${BASE_URL}/add`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(modifiedData),
      });

      const responseData = await response.json();
      if (response.ok) {
        // Update the fetchedData state after adding data
        const updatedFetchedData = [...fetchedData];
        updatedFetchedData.splice(selectedRowIndex, 1); // Remove the selected row
        setFetchedData(updatedFetchedData);

        // Clear the changes state after successful updates
        setChanges({});
        setSelectedRowIndex(-1); // Deselect the row
        alert(responseData.message);
      } else {
        alert("Error adding information.");
      }
    } catch (error) {
      console.error("Error adding information:", error);
    }
    // }
  };

  return (
    <Grid
      container
      justifyContent="center"
      alignItems="center"
      style={{ minHeight: "100vh" }}
    >
      <Paper
        elevation={10}
        sx={{
          padding: 3,
          height: 500,
          width: "97%",
          margin: "20px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {fetchedData.length === 0 ? (
          <p>No data available for showing.</p>
        ) : (
          <React.Fragment>
            <TableContainer component={Paper}>
              <Table>
                <TableHead
                  style={{
                    position: "sticky",
                    top: 0,
                    zIndex: 1,
                    backgroundColor: "#fff",
                  }}
                >
                  <TableRow>
                    <TableCell sx={{ minWidth: "60px" }}>
                      Customer Code
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Business Partner
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Hospital Name
                    </TableCell>
                    <TableCell sx={{ minWidth: "70px" }}>
                      Customer Type
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>Province</TableCell>
                    <TableCell sx={{ minWidth: "70px" }}>City</TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Equipment Type
                    </TableCell>
                    <TableCell sx={{ minWidth: "60px" }}>
                      Serial Number
                    </TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Install Date
                    </TableCell>
                    <TableCell sx={{ minWidth: "50px" }}>
                      Warranty Years
                    </TableCell>

                    <TableCell sx={{ minWidth: "80px" }}>
                      Machine Status
                    </TableCell>
                    <TableCell sx={{ minWidth: "60px" }}>
                      Contract Type
                    </TableCell>
                    <TableCell sx={{ minWidth: "60px" }}>Owner</TableCell>
                    <TableCell sx={{ minWidth: "80px" }}>
                      Arrival Date
                    </TableCell>
                    <TableCell sx={{ minWidth: "60px" }}>
                      Engineer Number
                    </TableCell>
                    <TableCell sx={{ minWidth: "50px" }}>
                      Engineer Name
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {fetchedData
                    .slice(startIndex, endIndex)
                    .map((item, index) => (
                      <TableRow
                        key={item.serial_number}
                        onClick={() => setSelectedRowIndex(index)}
                        sx={{
                          backgroundColor:
                            selectedRowIndex === index
                              ? "#f0f0f0"
                              : "transparent",
                          cursor: "pointer",
                        }}
                      >
                        <TableCell>
                          <textarea
                            type="number"
                            onChange={(event) =>
                              handleCellChange("customer_code", event)
                            }
                            style={{ width: "60px" }}
                          />
                        </TableCell>
                        <TableCell>
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("business_partner", event)
                            }
                            style={{ width: "80px" }}
                          />
                        </TableCell>
                        <TableCell>
                          {" "}
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("hospital_name", event)
                            }
                            style={{ width: "80px" }}
                          />
                        </TableCell>
                        <TableCell>
                          {" "}
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("customer_type", event)
                            }
                            style={{ width: "70px" }}
                          />
                        </TableCell>
                        <TableCell>
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("province", event)
                            }
                            style={{ width: "80px" }}
                          />
                        </TableCell>
                        <TableCell>
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("city", event)
                            }
                            style={{ width: "70px" }}
                          />
                        </TableCell>
                        <TableCell>{item.equipment_type}</TableCell>
                        <TableCell>{item.serial_number}</TableCell>
                        <TableCell>
                          {" "}
                          <input
                            type="date"
                            onChange={(event) =>
                              handleCellChange("install_day", event)
                            }
                            style={{ width: "110px" }}
                          />
                        </TableCell>
                        <TableCell>
                          <textarea
                            type="number"
                            onChange={(event) =>
                              handleCellChange(
                                "number_of_warranty_years",
                                event
                              )
                            }
                            style={{ width: "50px" }}
                          />
                        </TableCell>
                        <TableCell>
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("machine_status", event)
                            }
                            style={{ width: "80px" }}
                          />
                        </TableCell>
                        <TableCell>
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("contract_type", event)
                            }
                            style={{ width: "60px" }}
                          />
                        </TableCell>
                        <TableCell>
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("owner", event)
                            }
                            style={{ width: "60px" }}
                          />
                        </TableCell>
                        <TableCell>{formatDate(item.arrival_date)}</TableCell>
                        <TableCell>
                          <textarea
                            type="text"
                            onChange={(event) =>
                              handleCellChange("employee_number", event)
                            }
                            style={{ width: "60px" }}
                          />
                        </TableCell>
                        <TableCell>{selectedEngineerName}</TableCell>
                      </TableRow>
                    ))}
                </TableBody>
              </Table>
              <TablePagination
                rowsPerPageOptions={[1, 5, 10]}
                component="div"
                count={fetchedData.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={(event, newPage) => setPage(newPage)}
                onRowsPerPageChange={(event) => {
                  setRowsPerPage(parseInt(event.target.value, 10));
                  setPage(0);
                }}
              />
            </TableContainer>
            <Button
              type="submit"
              color="primary"
              variant="contained"
              onClick={handleAddClick}
              sx={{ margin: "10px auto" }}
            >
              Post
            </Button>
          </React.Fragment>
        )}
      </Paper>
    </Grid>
  );
}

export default AddDataPart1;
